import React, { useEffect, useState } from 'react';
import { Link, NavLink } from 'react-router-dom';
import styled from "styled-components";
import { FaUserCircle } from "react-icons/fa";
import Search from './Search';
// import iconUser from '../images/iconUser.svg';
const HeaderBlock = styled.header`
    padding : 16px;
    position : sticky;
    top : 0;
    background : #030303;
    z-index : 99999999;

    nav { 
        display : flex;
        justify-content : space-between;
        align-items : center;
    }
    nav .logo{
        font-size : 20px;
        letter-spacing : 8px;
    }
    .main-nav{
        display : flex;
        justify-content : space-between;
        list-style : none;
    }
    .main-nav li{
        padding : 0 8px;
        color : white;
        cursor : pointer;
        font-size : 18px;
        letter-spacing : 1px;
        text-transform : capitalize;
    }
    .profile{
        height : 30px;
        width : 30px;
    }

    @media screen and (max-width: 768px) {
        .account{
            display : none;
        }
    }
    @media screen and (max-width: 420px) {
        .account{
            display : none;
        }
        nav .logo{
            font-size : 16px;
            letter-spacing : 6px;
        }
        .main-nav li{
            font-size : 16px;
            padding : 0 6px;
        }
    }
`

export default function Header(){
    const [ search, setSearch ] = useState(false);
    const onSearch = () => {
        setSearch(!search);
    }
    
    return (
        <HeaderBlock>
            <nav className="site-nav">
                <Link className="logo" to="/app">SangBeom</Link>
                {/* <Link to="/"><img src={logo}/></Link> */}
                <ul className="main-nav">
                    <li>
                        <NavLink exact="true" to="/app">홈</NavLink>
                    </li>
                    <li>
                        <NavLink exact="true" to="/app/about">소개</NavLink>
                    </li>
                    <li onClick={onSearch}>
                        검색
                    </li>
                </ul>
                <div className="account">
                    <div className="profile">
                    <FaUserCircle style={{height : '100%', width : '100%', color : 'gray'}}/>
                        {/* <div className="profile-img">
                            <img src={iconUser}/>
                        </div> */}
                    </div>
                </div>
                {search && <Search onSearch={onSearch}/>}
            </nav>
        </HeaderBlock>
    );
};

