import React from 'react';
import styled from "styled-components";
import { FiMail, FiPhoneCall } from "react-icons/fi";
const ContentBlock = styled.div`
  padding : 60px 240px;
  text-align : center;
  color : gray;

  .intro {
    display : flex;
    justify-content : space-between;
    align-items : end;
  }
  .intro .profile{
    border-radius : 50%;
    overflow : hidden;
    width : 360px;
    height : 360px;
    position : relative;
    background : white;
  }
  .intro .profile img{
    position : absolute;
    top : 50%;
    left : 50%;
    transform : translate(-50%, -50%);
  }
  .intro h1{
    text-align : right;
    word-break : keep-all;
    line-height : 1.6;
  }

  .desc{
    text-align : left;
    word-break : keep-all
    line-height : 1.6;
    font-size : 20px,
  }
  h1{
    letter-spacing : 3px;
  }

  dl {
    margin-bottom : 32px;
  }
  dl:last-child{
    margin-bottom : 0;
  }
  dt {
    margin-bottom : 24px;
    font-size: 24px;
    letter-spacing : 2px;
  }

  dd img{
    height : 80px;
    margin : 0 12px;
  }

  li{
    font-size : 18px;
    padding : 4px 0;
  }

  @media screen and (max-width: 1240px) { 
    padding : 40px 3%;
  }
  @media screen and (max-width: 768px) { 
    .intro .profile{
      width : 300px;
      height : 300px;
    }
    .intro h1 {
      font-size : 26px;
      margin-bottom : 0;
    }
    .desc {
      line-height : 2;
    }
    li {
      font-size : 16px;
    }
  }

  @media screen and (max-width: 640px) {
    .intro{
      flex-direction : column;
      justify-content : center;
      align-items : center;
    }
    .intro h1 {
      margin-top : 16px;
      text-align : center;
    }

    dd img{
      height : 60px;
      margin : 0 8px;
    }
  }
`
const About = () => {
  return (
    <div>
      <ContentBlock>
        <div className="section intro">
          <div className="profile">
            <img src="https://beomoffice.github.io/app/testimg/myprofile.jpg" alt="나"/>
          </div>
          <h1>
            반갑습니다!<br/>
            저는 <span style={{color : '#4FFBDF'}}>깔끔한 UI</span>와<br/>
            <span style={{color : '#845EC2'}}>다양한 느낌의 UX</span>를 만드는<br/>
            웹 퍼블리셔 입니다.
          </h1>
        </div>
        <div className="section desc">
            저는 2년차 웹 퍼블리셔 박상범입니다. <br/>
            빡세게 일하고 시원하게 노는것이 삶의 모토입니다.<br/><br/>

            탄탄한 코드를 기반으로 하는 UI / UX를 추구하며, 어떻게 하면 더 간결하고 편하게 작업할 수 있는지에 관심이 많습니다.<br/>
            코드와 디자인, 그리고 사용자의 니즈를 어떠한 방식으로 맞춰야할지 생각을 많이 합니다.<br/>
            서로가 만족할만한 작업물을 만들어 내는 것이 어렵겠지만 만족할 수 있도록 해가는것이 저의 일이라고 생각합니다.<br/><br/>

            저는 제 일이 흥미롭고 즐겁습니다. 즐거우니 더 하고싶고, 더 잘하고싶습니다.<br/>
            좋아하는일을 프로페셔널하게 해내는 것,<br/><br/>
            그것이 저의 꿈입니다.
        </div>

        <div className="section">
          <h1>Stack</h1>
          <dl>
            <dt>Strong</dt>
            <dd>
              <img src="https://beomoffice.github.io/app/testimg/html.png" alt="html"/>
              <img src="https://beomoffice.github.io/app/testimg/css.png" alt="css"/>
              <img src="https://beomoffice.github.io/app/testimg/js.png" alt="js"/>
              <img src="https://beomoffice.github.io/app/testimg/jquery.png" alt="jquery"/>
            </dd>
          </dl>
          <dl>
            <dt>Experienced</dt>
            <dd>
              <img src="https://beomoffice.github.io/app/testimg/php.png" alt="php"/>
              <img src="https://beomoffice.github.io/app/testimg/react.png" alt="react"/>
            </dd>
          </dl>
        </div>

        <div className="section">
          <h1>Career</h1>
          <ul>
            <li>2018 ~ 2019 / 하이미디어종로점 UI/UX 수료(7개월)</li>
            <li>2019 / 에이블 인턴 (3개월)</li>
            <li>2019 ~ 2020 / 신비웹 사원 (7개월)</li>
            <li>2020 ~ 2021 / 어솔루션 사원 (9개월)</li>
          </ul>
        </div>

        <div className="section">
          <h1>CONTACT</h1>
          <ul>
            <li><FiMail style={{ marginRight : '8px', fontSize : 20, verticalAlign : 'bottom'}}/>beomoffice@gmail.com</li>
            <li><FiPhoneCall style={{ marginRight : '8px', fontSize : 20, verticalAlign : 'bottom'}}/>010-2797-7412</li>
          </ul>
        </div>
        
      </ContentBlock>
    </div>
  );
};

export default About;
